function sliderFeedback() {
    new Swiper(".feedback .mySwiper",{
        slidesPerView: 2,
        spaceBetween: 40,
        navigation: {
            nextEl: ".feedback .swiper-button-next",
            prevEl: ".feedback .swiper-button-prev"
        },
        pagination: {
            el: ".feedback .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 15,
            },
            993: {
                slidesPerView: 2,
                spaceBetween: 40,
            }
        }
    })
}

function sliderObjects() {
    new Swiper(".objects .mySwiper",{
        slidesPerView: 1,
        spaceBetween: 40,
        navigation: {
            nextEl: ".objects .swiper-button-next",
            prevEl: ".objects .swiper-button-prev"
        },
        pagination: {
            el: ".objects .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            993: {
                spaceBetween: 40,
            }
        }
    })
}

function sliderCalculator() {
    const swiper = new Swiper(".calculator .mySwiper", {
        slidesPerView: 1,
        spaceBetween: 40,
        navigation: {
            nextEl: ".calculator .swiper-button-next",
            prevEl: ".calculator .swiper-button-prev"
        },
        pagination: {
            el: ".calculator .swiper-pagination",
            type: "progressbar",
            clickable: true,
        },
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            993: {
                spaceBetween: 40,
            }
        },
        on: {
            slideChange: function () {
                const currentSlideIndex = this.activeIndex + 1;
                document.querySelector('.step-pagination__number').textContent = currentSlideIndex;
            }
        }
    });

    document.querySelector('.step-pagination__number').textContent = swiper.activeIndex + 1;
}

$(document).ready(function () {
    sliderFeedback();
    sliderObjects();
    sliderCalculator();

    //Menu
    $('.burger').click(function () {
        $('.header-mob').addClass('header-mob--active')
        $('.menu-bg').addClass('menu-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.header-mob__close').click(function () {
        $('.header-mob').removeClass('header-mob--active')
        $('.menu-bg').removeClass('menu-bg--active')
        $('body').removeClass('fixed-body')
    })

    $(".header__list li a").click((function() {
       $(".burger").removeClass("burger--active"),
       $(".header-mob").removeClass("header-mob--active")
       $("body").removeClass("fixed-body")
    }))

    var headerElement = $('.whatsapp');
    var headerHeight = headerElement.outerHeight();
    var isActive = false;

    $(window).on('scroll', function() {
        var scrollTop = $(this).scrollTop();

        if (scrollTop > headerHeight && !isActive) {
            headerElement.addClass('whatsapp-fixed');
            isActive = true;
        } else if (scrollTop <= headerHeight && isActive) {
            headerElement.removeClass('whatsapp-fixed');
            isActive = false;
        }
    });

    // Select
    $(".custom_select select").niceSelect()

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})